import React, { useEffect, useState } from 'react';
import './segment.scss';
import { SegmentModel } from '../../../_models/report';
import { Typography } from '@mui/material';

interface SegmentProps {
    segments: SegmentModel[];
    onSegmentClick: (index: number) => void;
    displayLabel?: boolean;
}

const SegmentControl: React.FC<SegmentProps> = ({ onSegmentClick, segments, displayLabel }) => {
    const [showLabel, setShowLabel] = useState<boolean>(true);

    useEffect(() => {
        if (displayLabel) setShowLabel(false);
    }, [])

    return (
        <div className='segment-background'>
            {segments.map((segment, index) => (
                <button className={segment.isSelected ? 'segment-button' : 'segment-button-inactive'}
                    key={index}
                    onClick={() => onSegmentClick(index)}
                >
                    {showLabel ?
                        <div className={`${segment.isSelected ? 'segment-label' : 'segment-label-inactive'} semiBold-18 segment_color`}><Typography className='semiBold-18'> {segment.name}</Typography></div>
                        :
                        <div className={`${segment.isSelected ? 'segment-label' : 'segment-label-inactive'} semiBold-18 segment_color`}> {segment.icon}
                            {
                                segment.isSelected ? <Typography className='p-0 semiBold-18 segment-label'>{segment.name}</Typography> : ""
                            }
                        </div>
                    }
                    {/* <div className={`${segment.isSelected ? 'segment-count' : 'segment-count-inactive'} section-header`}>{segment.count ?? ''}</div> */}
                </button>
            ))}
        </div>
    );
};
export default SegmentControl;